<template>
  <!-- メッセージエリア -->
  <div id="global-message">
    <b-alert variant="danger" dismissible show v-show="message.error" class="mb-0">
      {{ message.error }}
    </b-alert>
    <b-alert
      variant="warning" dismissible show v-show="message.warnings.length > 0" class="mb-0">
      <p v-for="warning in message.warnings" class="mb-0">{{ warning }}</p>
    </b-alert>
    <b-alert variant="info" dismissible show v-show="message.info" class="mb-0">
      {{ message.info }}
    </b-alert>
  </div>
</template>

<script>
export default {
  name: 'GlobalMessage',
  computed: {
    message: function () {
      return this.$store.state.messageData
    }
  }
}
</script>



